*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }
html {
  font-size: 62.5%; }
body {
  font-family: "Roboto";
  font-weight: 400;
  color: #777777;
  font-size: 1.6rem;
  letter-spacing: .2rem;
  padding: 5rem;
  background-color: white; }

.container {
  background-color: #F7F7F7;
  height: 50rem;
  width: 100%; }

.header {
  height: 95vh;
  background: linear-gradient(90.05deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../img/header.jpg) center / cover no-repeat;
  clip-path: polygon(0 1%, 100% 0, 100% 100%, 0 81%);
  position: relative;
  &__logo {
    position: absolute;
    left: 5rem;
    top: 5rem;
    height: 7rem;
    width: 5.6rem; }
  &__box {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center; } }
.title-first {
  color: white;
  &__main {
    display: block;
    font-weight: 800;
    font-size: 5rem;
    line-height: 6rem;
    letter-spacing: 1rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
    animation-name: LefttoRight;
    animation-duration: 2s;
    animation-delay: 1s;
    animation-timing-function: ease-in;
    backface-visibility: hidden; }
  &__secondary {
    display: block;
    font-weight: 100;
    font-size: 4rem;
    line-height: 4.8rem;
    letter-spacing: .5rem;
    animation-name: RightToLeft;
    animation-duration: 2s;
    animation-delay: 1s;
    animation-timing-function: ease-in;
    backface-visibility: hidden;
    margin-bottom: 6.5rem; } }



.btn {
  &:link, &:visited {
    color: #777777;
    background-color: white;
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.5rem 4rem;
    border-radius: 5rem;
    display: inline-block;
    transition: all .3s;
    position: relative;
    font-size: 2rem; }
  &:hover {
    transform: translateY(-4px);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3 );
    &::after {
      opacity: 0;
      transform: scaleX(1.6) scaleY(1.7); } }
  &:active {
    transform: translateY(-2px);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1  ); }
  &:after {
    content: "";
    background-color: white;
    width: 100%;
    height: 100%;
    border-radius: 50px;
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all .4s; }
  &_animated {
    animation: BtnToUp .5s ease-out .7s;
    animation-fill-mode: backwards; } }


@keyframes LefttoRight {
  0% {
    opacity: 0;
    transform: translateX(-100px); }
  //80%
  //  transform: translateX(10px)

  100% {
    opacity: 1;
    transform: translateX(0px); } }


@keyframes RightToLeft {
  0% {
    opacity: 0;
    transform: translateX(100px); }
  //80%
  //  transform: translateX(-10px)

  100% {
    opacity: 1;
    transform: translateX(0px); } }


@keyframes BtnToUp {
  0% {
    opacity: 0;
    transform: translateY(50px); }

  100% {
    opacity: 1;
    transform: translateY(0px); } }
